import React from 'react'
import AltHeroBanner from '../../components/banners/altHeroBanner';
import MyFooter from '../../components/myFooter';
import MyNavbar from '../../components/myNavbar';
import UploadSection from '../../components/uploadPastProject/uploadImageSection';


function UploadPastProjects(props) {

    return (
        <React.Fragment>
            <MyNavbar/>
            <AltHeroBanner props={props.props.heroBanner}/>
            <section class="section section-lg pt-sm-0 mt--100">
            <div class="container">
                <div class="justify-content-center row">
                    <div class="col-sm-12">
                        <UploadSection props={props.props}/>
                    </div>
                </div>
            </div>
        </section>
            <MyFooter/>
        </React.Fragment>
    );
  }
  
export default UploadPastProjects;