import React from "react";

const EnquiryForm = (props) => {
    
    const text = props.props
    return (
        <div class="bg-gradient-secondary shadow card">
            <div class="p-lg-5 card-body">
                <h4 class="mb-1 text-center">{text.contactTitle}</h4>
                <p class="mt-0 text-center">{text.contactSubtitle}</p>
                <div class="mt-5 form-group">
                    <div class="input-group-alternative input-group">
                        <div class="input-group-prepend">
                            <span class="input-group-text">
                                <i class="ni ni-user-run"></i>
                            </span>
                        </div>
                        <input placeholder="Your name" type="text" class="form-control"/>
                    </div>
                </div>
                <div class="form-group">
                    <div class="input-group-alternative input-group">
                        <div class="input-group-prepend">
                            <span class="input-group-text">
                                <i class="ni ni-email-83"></i>
                            </span>
                        </div>
                        <input placeholder="Email address" type="email" class="form-control"/>
                    </div>
                </div>
                <div class="mb-4 form-group">
                    <textarea cols="80" name="name" placeholder="Type a message..." rows="4" class="form-control-alternative form-control"></textarea>
                </div>
                <div>
                    <button type="button" class="btn-round btn btn-default btn-lg btn-block">Send Message</button>
                </div>
            </div>
        </div>
    );
};

export default EnquiryForm;