import React from "react";
import AltHeroBanner from "../../components/banners/altHeroBanner";
import MyFooter from "../../components/myFooter";
import MyNavbar from "../../components/myNavbar";
import ThreeTiles from "../../components/threeTiles";
import BulletPointBanner from "../../components/banners/bulletPointBanner";
import CallToAction from "../../components/callToAction";
import ContactSection from "../../components/contactSection";

function Home(props) {
  return (
      <React.Fragment>
            <MyNavbar/>
            <AltHeroBanner props={props.props.heroSection}/>
            <br/>
            <br/>
            <ThreeTiles props={props.props.heroSection}/>
            <BulletPointBanner props={props.props.bulletPointBanner}></BulletPointBanner>
            <CallToAction props={props.props.callToAction}></CallToAction>
            <ContactSection props={props.props.contactSection}></ContactSection>
            <MyFooter/>
      </React.Fragment>
  );
}

export default Home;
