import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import Home from './pages/home/homePage';
import About from './pages/about/aboutPage';
import { HomePageConsts } from './pages/home/homeConstants';
import PastProjects from './pages/pastProjects/pastProjectsPage';
import { AboutPageConsts } from './pages/about/aboutConstants';
import Blog from './pages/blog/blogPage';
import { BlogPageConsts } from './pages/blog/blogConstants';
import UploadPastProject from './pages/uploadPastProject/uploadPastProject';
import { UploadPastProjectConsts } from './pages/uploadPastProject/uploadPastProjectConstants';
import AllProjects from './pages/projects/projectsPage';
import { ProjectsPageConsts } from './pages/projects/projectsPageConsts';
import CreateBlogPost from './pages/createBlogPost/createBlogPost';
import { CreateBlogPostConsts } from './pages/createBlogPost/createBlogPostConsts';


ReactDOM.render(
  <Router>
    <Routes>
      <Route path="/" element={<Home props={HomePageConsts} />} />
      <Route path="/about" element={<About props={AboutPageConsts} />} />
      <Route path="/projects/all" element={<PastProjects props={HomePageConsts} />} />
      <Route path="/blog" element={<Blog props={BlogPageConsts} />} />
      <Route path="/admin/projects/all" element={<AllProjects props={ProjectsPageConsts}/>}/>
      <Route path="/admin/projects/upload" element={<UploadPastProject props={UploadPastProjectConsts}/>}/>
      <Route path="/admin/projects/upload/:project_id" element={<UploadPastProject props={UploadPastProjectConsts}/>}/>
      <Route path="/admin/blog/upload" element={<CreateBlogPost props={CreateBlogPostConsts}/>}/>
    </Routes>
  </Router>,

  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
