import React from "react";
import { useState } from "react";
import ReactMarkdown from "react-markdown";
import { Card, CardBody, CardHeader, Row, Col, Container, Form, Input} from "reactstrap";
import TextInput from "../uploadPastProject/textInput";
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';
import axios from "axios";

function BlogPostEditorContainer (props) {

    const [state, setState] = useState({
        post: '',
        selectedFile1 : null,
    
    }

    )

    const fileHandler = (event) => {
        var fd = new FormData();
        fd.append("image", event.target.files[0])
        axios.post(
            `${process.env.API_BASE_URL}/blog/create`,
            fd,{
            headers : {
                'Content-Type' : 'multipart/form-data',
                'Access-Control-Allow-Origin' : '*'
                }
            }
        ).then(res => {
            console.log(res)
        })
    }

    const UploadImageOne = (event) => {
        event.preventDefault()
        
        const req_body = {
            title : event.target.title.value,
            post : state.post
        }
    
        axios.post(
            "http://localhost:4000/blog/create",
            req_body,{
            headers : {
                'Content-Type' : 'application/json',
                'Access-Control-Allow-Origin' : '*'
                }
            }
        ).then(res => {
            console.log(res)
        })
    }


    return (
        <section className="section">
            <div className="row">
                <div className="col"></div>
                <div className="col-10">
                    <Card className="shadow mt--300">
                        <CardHeader>
                            <h2 className="text-center">Upload A Blog Post</h2>
                        </CardHeader>
                        <CardBody>
                            <Form onSubmit={UploadImageOne}> 
                            <Row>
                                <Col md="2">
                                    <h6 className="pt-2">Blog Title</h6>
                                </Col>
                                <Col>
                                    <Input
                                        className="form-control-alternative"
                                        placeholder={`Enter the blog post title`}
                                        type="title"
                                        name={`title`}
                                    />
                                </Col>
                            </Row>
                            <br></br>
                            <Row>
                                <Col md="2">
                                    <h6 className="pt-2">Photo 1</h6>
                                </Col>
                                <Col>
                                    <div className='btn btn-primary btn-block'>
                                        <input type='file' id='single' onChange={fileHandler}/>
                                        <i class="far fa-image"></i>
                                    </div>
                                </Col>
                            </Row>
                            <br/>
                            <Row>
                                <Col md="2">
                                    <h6 className="pt-2">Photo 1 Preview</h6>
                                </Col>
                                <Col>
                                    <Container className="text-center">
                                        <img alt="..." src={"http://178.62.92.215:4000/" + state.filePath1 } height={200} width={200}/>
                                    </Container>
                                </Col>
                            </Row>
                            <Row>
                                <Col md="2">
                                    <h6 className="pt-2">Photo 2</h6>
                                </Col>
                                <Col>
                                    <div className='btn btn-primary btn-block'>
                                        <input type='file' id='single' onChange={fileHandler}/>
                                        <i class="far fa-image"></i>
                                    </div>
                                </Col>
                            </Row>
                            <br/>
                            <Row>
                                <Col md="2">
                                    <h6 className="pt-2">Photo 2 Preview</h6>
                                </Col>
                                <Col>
                                    <Container className="text-center">
                                        <img alt="..." src={"http://178.62.92.215:4000/" } height={200} width={200}/>
                                    </Container>
                                </Col>
                            </Row>
                            <div class="BlogPostEditor">
                                <textarea className="blogTextEditor" autoFocus value={state.post} 
                                    onChange={
                                        (e) => {setState({...state, post : e.target.value})}
                                    }
                                />
                                <ReactMarkdown className="markdownEditor" children={state.post}/>
                            </div>
                            <br/>
                            <button type="submit" className={`btn btn-outline-success text-success btn-block`}>Upload Blog Post</button>
                            <br/>
                            </Form>
                        </CardBody>
                    </Card>
                </div>
                <div className="col"></div>
            </div>
        </section>
    );

}

