import React from "react";

// Reactstrap components
import {
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
  NavbarBrand,
  Navbar,
  NavItem,
  NavLink,
  Nav,
  NavbarToggler,
  Collapse,
  Row,
  Col,
  DropdownToggle
} from "reactstrap";

const MyNavbar = (props) => {
    
    const [isOpen, setIsOpen] = React.useState(false);

    return (
        <Navbar className="navbar-main navbar-transparent navbar-light headroom navbar navbar-expand-lg headroom--top headroom--not-bottom" >
            <NavbarBrand href="/"><img alt="..." src="../../assets/img/36FinalLogo.png"/> 36Boxes</NavbarBrand>
            <NavbarToggler onClick={() => { setIsOpen(!isOpen) }} />
            <Collapse isOpen={isOpen} navbar>
                <Row>
                    <Col>
                    <div class="collapse-close col">
                                <NavbarToggler onClick={() => {setIsOpen(!isOpen)}}>
                                    <span></span>
                                    <span></span>
                                </NavbarToggler>
                            </div>
                    </Col>
                </Row>
                <Nav className="mr-auto" navbar>
                    <NavItem>
                        <NavLink href="/about">About</NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink href="/blog">Blog</NavLink>
                    </NavItem>
                    <UncontrolledDropdown
                    inNavbar
                    nav
                    >
                        <DropdownToggle
                            caret
                            nav
                        >
                            Past Projects
                        </DropdownToggle>
                            <DropdownMenu right>
                                <DropdownItem>
                                <a class="d-flex align-items-center media" href="/past-projects">
                                    <div class="icon icon-shape bg-gradient-primary rounded-circle text-white">
                                        <i class="ni ni-spaceship"></i>
                                    </div>
                                    <div class="ml-3 media-body">
                                        <h6 class="heading text-primary mb-md-1">Websites</h6>
                                        <p class="description d-none d-md-inline-block mb-0">Learn how to use Argon compiling Scss, change brand colors and more.</p>
                                    </div>
                                </a>
                                </DropdownItem>
                                <DropdownItem>
                                <a class="d-flex align-items-center media" href="/past-projects">
                                    <div class="icon icon-shape bg-gradient-success rounded-circle text-white">
                                        <i class="ni ni-palette"></i>
                                    </div>
                                    <div class="ml-3 media-body">
                                        <h6 class="heading text-primary mb-md-1">Apps</h6>
                                        <p class="description d-none d-md-inline-block mb-0">Learn more about colors, typography, icons and the grid system we used for Argon.</p>
                                    </div>
                                </a>
                                </DropdownItem>
                                <DropdownItem>
                                <a class="d-flex align-items-center media" href="/past-projects">
                                    <div class="icon icon-shape bg-gradient-warning rounded-circle text-white">
                                        <i class="ni ni-ui-04"></i>
                                    </div>
                                    <div class="ml-3 media-body">
                                        <h5 class="heading text-warning mb-md-1">Misc Scripts</h5>
                                        <p class="description d-none d-md-inline-block mb-0">Browse our 50 beautiful handcrafted components offered in the Free version.</p>
                                    </div>
                                </a>
                                </DropdownItem>
                            </DropdownMenu>
                    </UncontrolledDropdown>

                    <UncontrolledDropdown
                    inNavbar
                    nav
                    >
                        <DropdownToggle
                            caret
                            nav
                        >
                            Login/Sign-up
                        </DropdownToggle>
                            <DropdownMenu right>
                                <DropdownItem>
                                Login
                                </DropdownItem>
                                <DropdownItem>
                                Sign-up
                                </DropdownItem>
                                <DropdownItem divider />
                                <DropdownItem>
                                Sign-out
                                </DropdownItem>
                            </DropdownMenu>
                    </UncontrolledDropdown>
                    </Nav>
            <ul class="align-items-lg-center ml-lg-auto navbar-nav">
                    <li class="nav-item">
                        <a href="https://www.instagram.com/36boxes" target="_blank" rel="noreferrer" class="nav-link-icon nav-link">
                            <i class="fa fa-instagram"></i>
                            <span class="nav-link-inner--text d-lg-none ml-2">Instagram</span>
                        </a>
                    </li>
                    <li class="nav-item">
                        <a href="https://twitter.com/36boxes" class="nav-link-icon nav-link">
                            <i class="fa fa-twitter-square"></i>
                            <span class="nav-link-inner--text d-lg-none ml-2">Twitter</span>
                        </a>
                    </li>
                    <li class="nav-item">
                        <a href="https://github.com/36boxes" class="nav-link-icon nav-link">
                            <i class="fa fa-github"></i>
                            <span class="nav-link-inner--text d-lg-none ml-2">Github</span>
                        </a>
                    </li>
                    <li class="d-none d-lg-block ml-lg-4 nav-item">
                        <a href="/" class="btn-neutral btn-icon btn btn-default">
                            <span class="btn-inner--icon">
                                <i class="far fa-paper-plane"></i>
                            </span>
                            <span class="nav-link-inner--text ml-1"> Get In Contact</span>
                        </a>
                    </li>
                </ul>
            </Collapse>
        </Navbar>
    );
};

export default MyNavbar;